<template lang="pug">
    v-list-item
        v-list-item-content
            v-list-item-title
                h3 {{fileName}}
            v-row
                v-col(cols="12" sm="12" lg="4")
                    span 当前状态：
                v-col(cols="12" sm="12" lg="4")
                    v-chip {{status}}
        v-list-item-action
            v-row
                v-col(cols="12" sm="12" lg="3")
                    v-btn(color="success" dark @click="start") 启动
                v-col(cols="12" sm="12" lg="3")
                    v-btn(color="warnning" dark @click="stop") 暂停
                v-col(cols="12" sm="12" lg="3")
                    v-btn(color="warnning" dark @click="edit") 编辑
                v-col(cols="12" sm="12" lg="3")
                    v-btn(color="error" dark @click="remove") 删除


</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    name: "dbmstatuslistitem",
    props:{
        fileName:{
            type:String,
            default:"",
        },
        status:{
            type:String,
            default:"",
        },
    },

    methods:{
        go_updateconfig(){
            this.$router.push({path:"/dbm/updateconfig"});
        },
        start(){
            api.StartDbmLocalConfig({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        stop(){
            api.StopDbm({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        remove(){
            api.RemoveDbmConfig({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        edit(){
            console.log(this.fileName)
            api.GetConfig({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("exbot/update_upexconfig",res.data)
                    this.go_updateconfig()
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    },



    
}
</script>


